@import url('https://fonts.googleapis.com/css2?family=Tomorrow:wght@300&display=swap');
@import "reset.css";

body {
  font-family: 'Tomorrow', sans-serif;
  background-color: #eee;
}

h1 {
  font-size: 3rem;
  font-weight: 600;
  padding: 0 20px;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
  padding: 5px 0;
}

.scroll {
  height: 40vh;
  overflow: scroll;
}

.full-width {
  width: 100%;
}

.d-flex {
  display: flex;
}

.header {
  align-items: center;
  height: 15vh;
  border-bottom: 2px solid #988dae;
}

.header,
.main,
footer {
  width: 60vw;
  margin: 0 auto;
  background-color: #f7f3ff;
}

.main {
  min-height: 78vh;
}

.scores-div,
.form-div {
  padding: 20px;
  width: 50%;
}

.scores-div-header {
  justify-content: space-between;
}

.score-list {
  border: 2px solid #988dae;
  background-color: #ede7f6;
  margin-top: 1em;
  min-height: 15vh;
}

.score-list,
.form-div,
form {
  flex-direction: column;
}

.form-div {
  align-items: flex-start;
}

form {
  align-items: flex-end;
  margin-top: 0.6em;
}

.score-list li {
  font-size: 1rem;
  font-weight: 600;
  padding: 5px;
}

.score-list li:nth-child(even) {
  background-color: #fff;
}

input {
  padding: 5px;
  font-size: 1.2rem;
  margin: 5px 0;
  border-radius: 5px;
  outline: none;
  border: 1px solid #808080;
}

button,
input[type=submit] {
  background-color: #988dae;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 3px 3px #808080;
}

button:hover,
input[type=submit]:hover {
  background-color: #f7f3ff;
  box-shadow: 1.5px 1.5px #808080;
}

footer {
  border-top: 1px solid #988dae;
  padding: 15px 0;
  text-align: center;
}
